import * as types from './data.types';

const initialState = {
  gridParams: null,
  users: null,
  userGroups: null,
  scenarios: null,
  blocks: null,
};

export const dataReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.FETCH_DATA:
      return { ...state, ...action.payload };
    default:
      return state;
  }
};
