import * as types from './content.types';

const initialState = {
  containerWidth: window.innerWidth,
  containerHeight: window.innerHeight,
  scenario: null,
};

export const contentReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.SET_SCENARIO:
      return { ...state, scenario: action.payload };
    case types.SET_CONTENT_CONTAINER_SIZES:
      return { ...state, ...action.payload };
    case types.SET_SCENARIO_PARAM:
      return { ...state, scenario: { ...state.scenario, ...action.payload } };
    default:
      return state;
  }
};
