import React from 'react';
import { Switch, Route } from 'react-router-dom';
import { Drawer } from '@material-ui/core';
import { useStyles } from './LeftDrawer.style';
import { ContentSettings } from '../ContentSettings/ContentSettings';
import { ScenarioEditor } from '../ScenarioEditor/ScenarioEditor';
import { BlockEditor } from '../BlockEditor/BlockEditor';
import { AdminSidebar } from '../AdminSidebar/AdminSidebar';
import { UsersList } from '../UsersList/UsersList';

export const LeftDrawer = () => {
  const classes = useStyles();

  return (
    <Drawer
      className={classes.drawer}
      variant="permanent"
      classes={{
        paper: classes.drawerPaper,
      }}
      anchor="left"
    >
      <Switch>
        <Route exact path="/admin">
          <AdminSidebar />
        </Route>
        <Route exact path="/admin/content-settings">
          <ContentSettings />
        </Route>
        <Route exact path="/admin/scenarios/:scenarioId">
          <ScenarioEditor />
        </Route>
        <Route exact path="/admin/scenarios/:scenarioId/block-editor/:blockId">
          <BlockEditor />
        </Route>
        <Route exact path="/admin/users">
          <UsersList />
        </Route>
      </Switch>
    </Drawer>
  );
};
