import React, { useEffect, useRef } from 'react';
import './FadeInBlock.css';

export const FadeInBlock = ({ children, modes, enableAnimation }) => {
  const domRef = useRef();
  const observer = useRef(null);
  const timeout = useRef(null);

  useEffect(() => {
    if (enableAnimation) {
      observer.current = new IntersectionObserver(entries => {
        entries.forEach(entry => {
          if (entry.isIntersecting) {
            domRef.current.classList.add('is-visible');
          }
        });
      });
      timeout.current = setTimeout(() => observer.current.observe(domRef.current), 1);
    }
  }, [enableAnimation]);

  useEffect(() => {
    return () => {
      if (observer.current && timeout.current) {
        clearTimeout(timeout.current);

        if (domRef.current) {
          /* eslint-disable */
          observer.current.unobserve(domRef.current);
          /* esling-enable */
        }
      }
    };
  }, []);

  return (
    <div className="fade-in-block" ref={domRef} style={modes.indexOf('view') === -1 ? { height: '100%' } : {}}>
      {children}
    </div>
  );
};
