import * as types from './blockEditor.types';

const initialState = {
  addBlockDialog: {
    show: false,
    order: null,
    direction: null,
  },
  selectedBlockId: null,
  highlightedBlockId: null,
};

export const blockEditorReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.TOGGLE_ADD_BLOCK_DIALOG:
      return { ...state, addBlockDialog: action.payload };
    case types.SET_SELECTED_BLOCK_ID:
      return { ...state, selectedBlockId: action.payload };
    case types.HIGHLIGHT_BLOCK:
      return { ...state, highlightedBlockId: action.payload };
    default:
      return state;
  }
};
