import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { calcBlockWidth } from '../../redux/Grid/grid.actions';
import './Text.css';

const splitStringIntoWordsAndPunctuationMarks = str => {
  const words = [...str.matchAll(/[a-z--]+/gi)];
  const parts = [];

  for (let i = 0; i < words.length; i++) {
    const { index } = words[i];
    const [word] = words[i];
    const wordLength = word.length;

    parts.push({
      word,
    });

    let punctuationMarks = '';

    if (words[i + 1]) {
      punctuationMarks = str.substring(index + wordLength, words[i + 1].index);
    } else {
      punctuationMarks = str.substring(index + wordLength, str.length);
    }

    parts.push({
      punctuationMarks,
    });
  }

  return parts;
};

export const Text = ({ block, fontSizes, setFontSizes }) => {
  const dispatch = useDispatch();
  const gridState = useSelector(state => state.grid);

  const renderParagraph = p => {
    const parts = splitStringIntoWordsAndPunctuationMarks(p);

    return parts.map((part, partIndex) =>
      part.word ? (
        <span key={partIndex} className="word">
          {part.word}
        </span>
      ) : (
        <span key={partIndex}>{part.punctuationMarks}</span>
      )
    );
  };

  //    
  //     ,         (, 50-70)

  const adjustFontSize = () => {
    const fontSizeAdjustment = document.querySelector(`#font-size-adjustment-${block.id}`);

    fontSizeAdjustment.style.display = 'block';

    const spans = fontSizeAdjustment.querySelectorAll('p span');
    const newFontSizes = [];

    for (
      let fontSize = gridState.sizes.minFontSize;
      fontSize <= Math.floor(gridState.sizes.rowHeight * 0.61803);
      fontSize++
    ) {
      spans.forEach(span => {
        span.style.fontSize = `${fontSize}px`;
      });

      let dy = 0;
      let charsInLineNumber = 0;

      for (const span of spans) {
        const { y } = span.getBoundingClientRect();

        if (dy === 0) {
          dy = y;
          continue;
        } else if (y !== dy) {
          break;
        }

        charsInLineNumber += span.textContent.length;
      }

      if (charsInLineNumber >= 50 && charsInLineNumber <= 70) {
        newFontSizes.push(fontSize);
      }

      if (charsInLineNumber < 50) {
        break;
      }
    }

    if (!newFontSizes.length) {
      newFontSizes.push(gridState.sizes.minFontSize);
    }

    fontSizeAdjustment.style.display = 'none';

    for (let i = newFontSizes.length - 1; i >= 0; i--) {
      if (fontSizes.indexOf(newFontSizes[i]) !== -1) {
        newFontSizes.splice(i, 1);
      }
    }

    if (fontSizes.indexOf(999) === -1) {
      setFontSizes(fontSizes => fontSizes.concat(newFontSizes));
      // setFontSizes([...fontSizes, ...newFontSizes]);
    }
  };

  useEffect(() => {
    adjustFontSize();
  });

  return block.text ? (
    <div
      className="content-block-item text-container"
      style={{
        width: block.params.textWidth !== undefined ? `${dispatch(calcBlockWidth(block.params.textWidth))}px` : 'auto',
        top: `calc(var(--grid-row-height) * ${gridState.focusRowIndex - 1})`,
      }}
    >
      <div id={`font-size-adjustment-${block.id}`} className="text">
        <p>{renderParagraph(block.text[0])}</p>
      </div>
      {block.title && <div className="content-block-item-title">{block.title}</div>}
      <div className="text">
        {block.text.map((p, pIndex) => (
          <p key={pIndex}>{renderParagraph(p)}</p>
        ))}
      </div>
    </div>
  ) : null;
};

