// import { batch } from 'react-redux';
import * as types from './data.types';
import data from '../../data.json';

export const fetchData = () => dispatch => {
  dispatch({
    type: types.FETCH_DATA,
    payload: {
      gridParams: data.gridParams,
      users: dispatch(normalizeArray(data.users)),
      userGroups: dispatch(normalizeArray(data.userGroups)),
      scenarios: dispatch(normalizeArray(data.scenarios)),
      blocks: dispatch(normalizeArray(data.blocks)),
    },
  });
};

export const normalizeArray = array => () => {
  const normalizedArray = {};

  array.forEach(item => {
    normalizedArray[item.id] = item;
  });

  return normalizedArray;
};

export const getScenarioById = id => (dispatch, getState) => {
  if (!id) {
    return null;
  }

  const { data } = getState();
  const scenario = data.scenarios[id];

  return {
    ...scenario,
    blocks: dispatch(normalizeArray(scenario.blocks.map(blockId => data.blocks[blockId]))),
  };
};
