import React, { useEffect } from 'react';
import { Switch, Route, useLocation } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { selectBlock } from '../../redux/BlockEditor/blockEditor.actions';
import { ContentContainer } from '../ContentContainer/ContentContainer';
import { LeftDrawer } from '../LeftDrawer/LeftDrawer';
import { Scenario } from '../Scenario/Scenario';
import { MonitoringResult } from '../MonitoringResult/MonitoringResult';

import './Admin.css';

export const Admin = () => {
  const location = useLocation();
  const dispatch = useDispatch();
  const blockEditorState = useSelector(state => state.blockEditor);
  const gridState = useSelector(state => state.grid);

  useEffect(() => {
    if (blockEditorState.selectedBlockId) {
      const pathnameParts = location.pathname.split('/');
      const pathBlockEditorIndex = pathnameParts.indexOf('block-editor');

      if (pathBlockEditorIndex === -1 || !pathnameParts[pathBlockEditorIndex + 1]) {
        dispatch(selectBlock(null));
      }
    }
  }, [blockEditorState.selectedBlockId, dispatch, location.pathname]);

  const exampleBlock = {
    type: 'text-image-horizontal',
    title: '  ',
    text: [
      '      .            ,            .       ,     : ,  ,    .',
      '        ,  ,  .',
      '   ,     ,      .',
    ],
    image: {
      height: 0,
    },
    params: {
      width: gridState.sizes.columnsNumber,
      textWidth: Math.floor(0.61803 * gridState.sizes.columnsNumber),
    },
  };

  return (
    <div className="admin">
      <Switch>
        <Route path="/admin">
          <LeftDrawer />
          <Switch>
            <Route exact path="/admin/content-settings">
              <ContentContainer blocks={[exampleBlock]} modes={['example']} />
            </Route>
            <Route path={['/admin/scenarios/:scenarioId', '/admin/scenarios/:scenarioId/block-editor/:blockId']}>
              <Scenario modes={['edit']} />
            </Route>
            <Route path="/admin/users/:userId/monitoring-result/:resultId">
              <MonitoringResult />
            </Route>
          </Switch>
        </Route>
      </Switch>
    </div>
  );
};
