import React, { useState } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { Menu, MenuItem, IconButton, Divider } from '@material-ui/core';
import {
  MoreHoriz,
  ArrowUpward,
  ArrowDownward,
  VerticalAlignTop,
  VerticalAlignBottom,
  Edit,
  Delete,
} from '@material-ui/icons';
import { useStyles } from './BlockActions.style';
import { setScenarioBlocks } from '../../redux/Content/content.actions';

import './BlockActions.css';
import { toggleAddBlockDialog } from '../../redux/BlockEditor/blockEditor.actions';

export const BlockActions = ({ block, canMoveUp, canMoveDown }) => {
  const dispatch = useDispatch();
  const history = useHistory();
  const contentState = useSelector(state => state.content);
  const { scenario } = contentState;
  const blocks = scenario ? scenario.blocks : null;
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = useState(null);
  const isMenuOpen = Boolean(anchorEl);

  const handleMenuOpen = e => {
    setAnchorEl(e.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  const moveBlockUp = () => {
    let maxMinBlock;

    for (const [, b] of Object.entries(blocks)) {
      if (!maxMinBlock && b.order < block.order) {
        maxMinBlock = b;
      }

      if (maxMinBlock && b.order < block.order && b.order > maxMinBlock.order) {
        maxMinBlock = b;
      }
    }

    blocks[block.id].order = maxMinBlock.order;
    blocks[maxMinBlock.id].order = block.order;

    handleMenuClose();
    dispatch(setScenarioBlocks(blocks));
  };

  const moveBlockDown = () => {
    let minMaxBlock;

    for (const [, b] of Object.entries(blocks)) {
      if (!minMaxBlock && b.order > block.order) {
        minMaxBlock = b;
      }

      if (minMaxBlock && b.order > block.order && b.order < minMaxBlock.order) {
        minMaxBlock = b;
      }
    }

    blocks[block.id].order = minMaxBlock.order;
    blocks[minMaxBlock.id].order = block.order;

    handleMenuClose();
    dispatch(setScenarioBlocks(blocks));
  };

  const removeBlock = () => {
    delete blocks[block.id];
    handleMenuClose();
    dispatch(setScenarioBlocks(blocks));
    history.push(`/admin/scenarios/${scenario.id}`);
  };

  const addBlock = direction => {
    handleMenuClose();
    dispatch(toggleAddBlockDialog(block.order, direction));
  };

  return (
    <>
      <div className="content-block-actions-container">
        <IconButton color="inherit" size="small" onClick={handleMenuOpen}>
          <MoreHoriz />
        </IconButton>
      </div>
      <Menu
        className={classes.menu}
        anchorEl={anchorEl}
        anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
        keepMounted
        transformOrigin={{ vertical: 'top', horizontal: 'right' }}
        open={isMenuOpen}
        onClose={handleMenuClose}
      >
        <MenuItem
          component={Link}
          to={`/admin/scenarios/${scenario.id}/block-editor/${block.id}`}
          onClick={handleMenuClose}
        >
          <Edit />
          
        </MenuItem>
        {canMoveUp && (
          <MenuItem onClick={moveBlockUp}>
            <ArrowUpward />
             
          </MenuItem>
        )}
        {canMoveDown && (
          <MenuItem onClick={moveBlockDown}>
            <ArrowDownward />
             
          </MenuItem>
        )}
        <MenuItem className={classes.remove} onClick={removeBlock}>
          <Delete />
          
        </MenuItem>
        <Divider className={classes.divider} />
        <MenuItem onClick={() => addBlock(-1)}>
          <VerticalAlignTop />
            
        </MenuItem>
        <MenuItem onClick={() => addBlock(1)}>
          <VerticalAlignBottom />
            
        </MenuItem>
      </Menu>
    </>
  );
};
