import React from 'react';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { Grid, Button } from '@material-ui/core';
import { useStyles } from './Scenarios.style';

export const Scenarios = () => {
  const classes = useStyles();
  const dataState = useSelector(state => state.data);
  const { scenarios } = dataState;

  return scenarios ? (
    <Grid className={classes.root} container justify="center" alignItems="center">
      <Grid item container xs={12} spacing={2} justify="center" style={{ maxWidth: 1280 }}>
        {Object.entries(scenarios).map(([, scenario]) => (
          <Grid key={scenario.id} item xs={4}>
            <Button
              className={classes.scenario}
              variant="outlined"
              component={Link}
              to={`/scenarios/${scenario.id}`}
              fullWidth
            >
              {scenario.title}
            </Button>
          </Grid>
        ))}
      </Grid>
    </Grid>
  ) : null;
};
