import * as types from './grid.types';

const atom = 8;
const gutter = atom * 4;

const initialState = {
  initialized: false,
  sizes: {
    gutter,
    columnsNumber: 12,
    columnWidth: 0,
    rowsNumber: 0,
    rowHeight: gutter - atom,
    minFontSize: 0,
    maxWidth: 1280,
  },
  focusRowIndex: 0,
};

export const gridReducer = (state = initialState, action) => {
  switch (action.type) {
    case types.FETCH_GRID_PARAMS:
      return { ...action.payload };
    case types.RESIZE_GRID:
      return { ...state, sizes: { ...state.sizes, ...action.payload } };
    case types.TOGGLE_GRID_DISPLAY:
      return { ...state, display: { ...state.display, ...action.payload } };
    case types.SET_FOCUS_ROW_INDEX:
      return { ...state, focusRowIndex: action.payload };
    default:
      return state;
  }
};
