import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';

import './Grid.css';

export const Grid = ({ show }) => {
  const gridState = useSelector(state => state.grid);

  const columns = [];
  const rows = [];

  for (let i = 0; i < gridState.sizes.columnsNumber * 2 - 1; i++) {
    columns.push(<div key={i} className={`grid-column ${i % 2 !== 0 ? 'gutter' : ''}`}></div>);
  }

  for (let i = 0; i < gridState.sizes.rowsNumber; i++) {
    const classes = ['grid-row'];

    if (i === gridState.focusRowIndex) {
      classes.push('grid-row-focus');
    }

    rows.push(<div key={i} className={classes.join(' ')}></div>);
  }

  // set & update css variables

  useEffect(() => {
    const { columnsNumber, rowsNumber, columnWidth, rowHeight, gutter } = gridState.sizes;
    const props = [
      {
        prop: '--grid-columns-number',
        value: columnsNumber,
      },
      {
        prop: '--grid-rows-number',
        value: rowsNumber,
      },
      {
        prop: '--grid-column-width',
        value: `${columnWidth}px`,
      },
      {
        prop: '--grid-row-height',
        value: `${rowHeight}px`,
      },
      {
        prop: '--grid-gutter',
        value: `${gutter}px`,
      },
    ];

    props.forEach(({ prop, value }) => {
      document.documentElement.style.setProperty(prop, value);
    });
  });

  return (
    <div className={`grid ${show ? 'show-grid' : ''}`}>
      <div className="grid-columns">{columns.map(column => column)}</div>
      <div className="grid-rows">{rows.map(row => row)}</div>
    </div>
  );
};
