import React, { useRef } from 'react';
import './GazeMap.css';

export const GazeMap = ({ monitoringResult }) => {
  const canvasRef = useRef(null);
  const { contentMarkup, gazeMap } = monitoringResult;
  const { width, height, y, left, top } = contentMarkup.position;

  const renderGazeMap = () => {
    const canvas = canvasRef.current;
    const c = canvas.getContext('2d');

    c.clearRect(0, 0, width, height);
    c.save();
    c.translate(-left, -y - 100);

    c.font = `${contentMarkup.fontSize}px 'Inter'`;
    c.textAlign = 'left';
    c.textBaseline = 'top';

    contentMarkup.blocks.forEach(({ position, words }) => {
      const { x, y, width, height } = position;

      c.fillStyle = '#ddd';
      c.fillRect(x, y, width, height);

      words.forEach(({ position, word }) => {
        const { x, y } = position;

        // c.beginPath();
        // c.arc(x, y, 1, 0, Math.PI * 2);
        // c.closePath();
        // c.fill();

        c.fillStyle = '#000';
        c.fillText(word, x, y);
      });
    });

    c.restore();
    c.save();
    c.translate(-left, 0);

    c.lineWidth = 2;
    c.strokeStyle = 'red';

    c.beginPath();

    Object.entries(gazeMap.points).forEach(([i, point]) => {
      const { x, y } = point;

      if (i === 0) {
        c.moveTo(x, y);
      } else {
        c.lineTo(x, y);
      }
    });

    c.stroke();
    c.closePath();

    c.fillStyle = 'rgba(255, 0, 0, 0.3)';

    Object.entries(gazeMap.points).forEach(([, point]) => {
      const { x, y, r } = point;

      c.beginPath();
      c.arc(x, y, r, 0, Math.PI * 2);
      c.closePath();
      c.fill();
    });

    c.restore();
  };

  if (canvasRef.current) {
    renderGazeMap();
  }

  return <canvas ref={canvasRef} width={width} height={height} style={{ maxWidth: '100%' }} />;
};
