import { batch } from 'react-redux';
import { setScenarioBlock } from '../Content/content.actions';
import * as types from './blockEditor.types';

export const highlightBlock = id => dispatch => {
  dispatch({ type: types.HIGHLIGHT_BLOCK, payload: id });
};

export const selectBlock = blockId => dispatch => {
  batch(() => {
    dispatch({ type: types.SET_SELECTED_BLOCK_ID, payload: blockId });
    dispatch(highlightBlock(blockId));
  });
};

export const toggleAddBlockDialog = (order = null, direction = null) => (dispatch, getState) => {
  const { blockEditor } = getState();
  dispatch({
    type: types.TOGGLE_ADD_BLOCK_DIALOG,
    payload: { show: !blockEditor.addBlockDialog.show, order, direction },
  });
};

export const setBlockParam = (param, value) => (dispatch, getState) => {
  const { blockEditor, content } = getState();
  const block = { ...content.scenario.blocks[blockEditor.selectedBlockId] };
  const params = param.split('.');

  if (params.length > 1) {
    block[params[0]][params[1]] = value;
  } else {
    block[params[0]] = value;
  }

  dispatch(setScenarioBlock(block));
};
