import { batch } from 'react-redux';
import * as types from './grid.types';
import { setContentContainerSizes } from '../Content/content.actions';

export const fetchGridParams = () => (dispatch, getState) => {
  const { grid, data } = getState();
  const { focusRowIndex } = data.gridParams;
  const gridParams = {
    ...grid,
    initialized: true,
    focusRowIndex,
    currentRowIndex: focusRowIndex,
    sizes: { ...grid.sizes },
  };

  Object.keys(data.gridParams.sizes).forEach(key => {
    gridParams.sizes[key] = data.gridParams.sizes[key];
  });

  dispatch({
    type: types.FETCH_GRID_PARAMS,
    payload: gridParams,
  });
};

export const setGridSizes = sizes => dispatch => {
  batch(() => {
    dispatch({ type: types.RESIZE_GRID, payload: sizes });
    dispatch(resizeGrid());
  });
};

export const calcColumnWidth = () => (_, getState) => {
  const { grid, content } = getState();
  const { gutter, columnsNumber } = grid.sizes;

  return Math.floor((content.containerWidth - gutter * (columnsNumber - 1)) / columnsNumber);
};

export const calcBlockWidth = (columnsNumber, columnWidth) => (_, getState) => {
  const { grid } = getState();
  const { gutter } = grid.sizes;

  if (!columnWidth) {
    columnWidth = grid.sizes.columnWidth;
  }

  return columnWidth * columnsNumber + gutter * (columnsNumber - 1);
};

export const resizeGrid = () => (dispatch, getState) => {
  const { content, grid } = getState();
  const { columnsNumber, rowHeight } = grid.sizes;
  const columnWidth = dispatch(calcColumnWidth());
  const rowsNumber = Math.floor(content.containerHeight / rowHeight);

  let { focusRowIndex } = grid;

  batch(() => {
    const newContainerWidth = dispatch(calcBlockWidth(columnsNumber, columnWidth));

    if (newContainerWidth !== content.containerWidth) {
      dispatch(setContentContainerSizes(newContainerWidth, content.containerHeight));
    }

    if (focusRowIndex > rowsNumber - 1) {
      focusRowIndex = rowsNumber - 1;
      dispatch(setGridFocusRowIndex(focusRowIndex));
    }

    dispatch({ type: types.RESIZE_GRID, payload: { rowsNumber, columnWidth } });
  });
};

export const toggleGridDisplay = param => dispatch => {
  dispatch({ type: types.TOGGLE_GRID_DISPLAY, payload: param });
};

export const setGridFocusRowIndex = index => dispatch => {
  batch(() => {
    dispatch({ type: types.SET_FOCUS_ROW_INDEX, payload: index });
    dispatch(resizeGrid());
  });
};
