import React, { useEffect } from 'react';
import { useParams, Link, Redirect } from 'react-router-dom';
import {
  Slider,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Typography,
  Divider,
  IconButton,
} from '@material-ui/core';
import { Close } from '@material-ui/icons';
import { useDispatch, useSelector } from 'react-redux';
import { selectBlock, setBlockParam } from '../../redux/BlockEditor/blockEditor.actions';
import { useStyles } from '../LeftDrawer/LeftDrawer.style';

export const BlockEditor = () => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const gridState = useSelector(state => state.grid);
  const contentState = useSelector(state => state.content);
  const blockEditorState = useSelector(state => state.blockEditor);
  const { blockId, scenarioId } = useParams();
  const { scenario } = contentState;
  const block = scenario ? scenario.blocks[blockId] : null;

  const handleBlockParam = (param, value, prevValue) => {
    if (value === prevValue) {
      return;
    }

    dispatch(setBlockParam(param, value));
  };

  useEffect(() => {
    if (scenario && (!blockEditorState.selectedBlockId || blockEditorState.selectedBlockId !== blockId)) {
      dispatch(selectBlock(blockId));
    }
  }, [block, dispatch, blockId, scenario, blockEditorState.selectedBlockId]);

  if (scenario && !block) {
    return <Redirect to={`/admin/scenarios/${scenario.id}`} />;
  }

  return gridState.sizes.rowsNumber && block ? (
    <div className="block-editor">
      <div className={classes.drawerItem}>
        <div className={classes.drawerHeader}>
          <Typography variant="h6"> </Typography>
          <IconButton component={Link} to={`/admin/scenarios/${scenarioId}`}>
            <Close />
          </IconButton>
        </div>
      </div>
      <Divider />
      <div className={classes.drawerItem}>
        <Typography className={classes.drawerItemTitle} variant="h6">
           
        </Typography>
        <div className={classes.drawerItemParam}>
          <Typography variant="body1"> </Typography>
          <Slider
            color="primary"
            value={block.params.width ? block.params.width : block.params.textWidth}
            min={3}
            max={gridState.sizes.columnsNumber}
            step={1}
            marks={[
              {
                value: 3,
                label: '3',
              },
              {
                value: gridState.sizes.columnsNumber,
                label: `${gridState.sizes.columnsNumber}`,
              },
            ]}
            valueLabelDisplay="auto"
            onChange={(e, value) =>
              handleBlockParam(
                block.params.width ? 'params.width' : 'params.textWidth',
                value,
                block.params.width ? block.params.width : block.params.textWidth
              )
            }
          />
        </div>
        {block.type === 'text-image-horizontal' && (
          <div className={classes.drawerItemParam}>
            <Typography variant="body1">    </Typography>
            <Slider
              className={classes.slider}
              color="primary"
              value={block.params.textWidth}
              min={0}
              max={block.params.width}
              step={1}
              marks={[
                {
                  value: 0,
                  label: <span className={classes.sliderMarkLeft}></span>,
                },
                {
                  value: Math.floor((1 - 0.61803) * block.params.width),
                  label: (
                    <span>
                      
                      <br />
                      
                    </span>
                  ),
                },
                {
                  value: Math.floor(0.61803 * block.params.width),
                  label: (
                    <span>
                      
                      <br />
                      
                    </span>
                  ),
                },
                {
                  value: block.params.width,
                  label: <span className={classes.sliderMarkRight}></span>,
                },
              ]}
              valueLabelDisplay="auto"
              onChange={(e, value) => handleBlockParam('params.textWidth', value, block.params.textWidth)}
            />
          </div>
        )}
      </div>
      <Divider />
      {block.type.indexOf('text') !== -1 && (
        <>
          <div className={classes.drawerItem}>
            <Typography className={classes.drawerItemTitle} variant="h6">
               
            </Typography>
            <div className={classes.drawerItemParam}>
              <TextField
                label=""
                variant="outlined"
                fullWidth
                value={block.title}
                onChange={e => handleBlockParam('title', e.target.value, block.title)}
              />
            </div>
            <div className={classes.drawerItemParam}>
              <TextField
                label=" "
                multiline
                variant="outlined"
                fullWidth
                value={block.text.join('\n')}
                onChange={e => handleBlockParam('text', e.target.value.split('\n'), block.text)}
              />
            </div>
          </div>
          {block.type.indexOf('text-image') !== -1 && <Divider />}
        </>
      )}
      {block.type.indexOf('image') !== -1 && (
        <div className={classes.drawerItem}>
          <Typography className={classes.drawerItemTitle} variant="h6">
            
          </Typography>
          <div className={classes.drawerItemParam}>
            <TextField
              label=""
              variant="outlined"
              fullWidth
              value={block.image.src}
              onChange={e => handleBlockParam('image.src', e.target.value, block.image.src)}
            />
          </div>
          <div className={classes.drawerItemParam}>
            <FormControl variant="outlined" fullWidth>
              <InputLabel id="block-image-size"></InputLabel>
              <Select
                labelId="block-image-size"
                label=""
                value={block.image.size ? block.image.size : 'cover'}
                onChange={e =>
                  handleBlockParam('image.size', e.target.value, block.image.size ? block.image.size : 'cover')
                }
              >
                <MenuItem value="cover"></MenuItem>
                <MenuItem value="contain"></MenuItem>
              </Select>
            </FormControl>
          </div>
          {block.type === 'text-image-horizontal' && (
            <div className={classes.drawerItemParam}>
              <FormControl variant="outlined" fullWidth>
                <InputLabel id="block-image-position"></InputLabel>
                <Select
                  labelId="block-image-position"
                  label=""
                  value={block.image.position ? block.image.position : 'right'}
                  onChange={e =>
                    handleBlockParam(
                      'image.position',
                      e.target.value,
                      block.image.position ? block.image.position : 'right'
                    )
                  }
                >
                  <MenuItem value="right"></MenuItem>
                  <MenuItem value="left"></MenuItem>
                </Select>
              </FormControl>
            </div>
          )}
          <div className={classes.drawerItemParam}>
            <Typography variant="body1"> </Typography>
            <Slider
              className={classes.slider}
              color="primary"
              value={block.image.height ? block.image.height : 0}
              min={0}
              max={gridState.sizes.rowsNumber}
              step={1}
              marks={[
                {
                  value: 0,
                  label: <span className={classes.sliderMarkLeft}></span>,
                },
                {
                  value: gridState.sizes.rowsNumber,
                  label: `${gridState.sizes.rowsNumber}`,
                },
              ]}
              valueLabelDisplay="auto"
              onChange={(e, value) =>
                handleBlockParam('image.height', value, block.image.height ? block.image.height : 0)
              }
            />
          </div>
        </div>
      )}
    </div>
  ) : null;
};
