import React from 'react';
import { Image } from '../Image/Image';
import { Text } from '../Text/Text';

import './TextImageVertical.css';

export const TextImageVertical = ({ block, fontSizes, setFontSizes }) => {
  return (
    <>
      <Image block={block} />
      <Text block={block} fontSizes={fontSizes} setFontSizes={setFontSizes} />
    </>
  );
};
