import React, { useRef, useState, useEffect, useCallback } from 'react';
import { useSelector } from 'react-redux';
import { Button } from '@material-ui/core';
import { usePrevious } from '../../hooks';

import { Block } from '../Block/Block';

import './Content.css';

const transitionFunction = 'linear';
const scrollTimeMin = 200;
const scrollTimeMax = 1500;
const initializeTime = 500;
const intervalSpeed = 1000 / 30;

export const Content = ({ modes, blocks, autoScroll }) => {
  const prevBlocks = usePrevious(JSON.parse(JSON.stringify(blocks)));
  const blockEditorState = useSelector(state => state.blockEditor);
  const gridState = useSelector(state => state.grid);
  const { sizes } = gridState;
  const prevSizes = usePrevious({ ...sizes });
  const contentRef = useRef(null);
  const [blocksGroups, setBlocksGroups] = useState(null);
  const contentStyle = useRef({ top: 0 });
  const isScrollBlocked = useRef(false);
  const currentRowIndex = useRef(gridState.focusRowIndex);
  const shouldUpdate = useRef(0);
  const interval = useRef(null);
  const gazePosition = useRef({ x: 0, y: 0, px: 0, py: 0, r: 10 });
  const gazeMap = useRef({
    points: {},
  });
  const contentMarkup = useRef({});
  const startDate = useRef(new Date());
  const [fontSizes, setFontSizes] = useState([]);
  const fontSizeAdjustmentsNumber = useRef(0);
  const [enableAnimation, setEnableAnimation] = useState(false);

  //           (     )

  useEffect(() => {
    if (JSON.stringify(sizes) !== JSON.stringify(prevSizes)) {
      fontSizeAdjustmentsNumber.current = 0;
      setFontSizes([]);
    } else if (fontSizes.length) {
      if (fontSizeAdjustmentsNumber.current <= Object.keys(blocks).length) {
        let sortedFontSizes = [...fontSizes].sort((a, b) => a - b);

        for (let i = sortedFontSizes.length - 1; i >= 0; i--) {
          if (sortedFontSizes[i] === 999) {
            sortedFontSizes.splice(i, 1);
          }
        }

        sortedFontSizes = [...new Set(sortedFontSizes)];

        // let fontSize = sortedFontSizes[Math.floor(sortedFontSizes.length / 2)];
        let fontSize = Math.floor(sortedFontSizes.reduce((prev, value) => prev + value, 0) / sortedFontSizes.length);

        document.documentElement.style.setProperty('--font-size', `${fontSize}px`);
        document.documentElement.style.setProperty('--font-size-title', `${Math.floor(fontSize * 1.61803)}px`);

        fontSizeAdjustmentsNumber.current++;

        if (fontSizeAdjustmentsNumber.current >= Object.keys(blocks).length) {
          setFontSizes(fontSizes => fontSizes.concat(999));
        }
      } else if (contentRef.current && !enableAnimation) {
        setTimeout(() => {
          if (!contentRef.current) {
            return;
          }

          const { right, bottom, left, width, height, x, y } = contentRef.current.getBoundingClientRect();

          contentMarkup.current = {
            position: {
              top: parseInt(contentRef.current.style.top),
              right,
              bottom,
              left,
              width,
              height,
              x,
              y,
            },
            initialTop: gridState.sizes.rowHeight * (gridState.focusRowIndex - 1),
            fontSize: 0,
            blocks: [],
          };

          document.querySelectorAll('.content-block').forEach(block => {
            const { x, y, width, height } = block.getBoundingClientRect();

            contentMarkup.current.blocks.push({
              position: {
                x: Math.round(x),
                y: Math.round(y),
                width: Math.round(width),
                height: Math.round(height),
              },
              words: [...block.querySelectorAll('.word')].map(word => {
                const { x, y, width, height } = word.getBoundingClientRect();

                contentMarkup.current.fontSize = parseInt(window.getComputedStyle(word).fontSize);

                return {
                  word: word.textContent,
                  position: {
                    x: Math.round(x),
                    y: Math.round(y),
                    width: Math.round(width),
                    height: Math.round(height),
                  },
                };
              }),
            });
          });

          setEnableAnimation(true);
        }, initializeTime);
      }
    }
  }, [
    blocks,
    enableAnimation,
    fontSizes,
    gridState.focusRowIndex,
    gridState.sizes.minFontSize,
    gridState.sizes.rowHeight,
    prevSizes,
    setFontSizes,
    sizes,
  ]);

  const toggleContentScroll = value => {
    isScrollBlocked.current = value;
  };

  const autoScrollContent = useCallback(() => {
    const contentElement = contentRef.current;

    if (
      currentRowIndex.current === null ||
      currentRowIndex.current === gridState.focusRowIndex ||
      isScrollBlocked.current
    ) {
      return;
    }

    const scrollTimeDifference = scrollTimeMax - scrollTimeMin;
    let time;
    let scrollStep;

    if (currentRowIndex.current < gridState.focusRowIndex) {
      time = scrollTimeMin + scrollTimeDifference * (currentRowIndex.current / (gridState.focusRowIndex - 1));
      scrollStep = -gridState.sizes.rowHeight;
    } else if (currentRowIndex.current > gridState.focusRowIndex) {
      time = scrollTimeMax - scrollTimeDifference * (currentRowIndex.current / (gridState.sizes.rowsNumber - 1));
      scrollStep = gridState.sizes.rowHeight;
    }

    time = Math.round(time);

    // prevent infinite scroll to top

    const newTop = contentStyle.current.top - scrollStep;

    if (newTop > gridState.sizes.rowHeight * gridState.focusRowIndex) {
      return;
    }

    // prevent infinite scroll to bottom

    if (newTop + contentElement.offsetHeight < gridState.sizes.rowHeight * (gridState.focusRowIndex + 1)) {
      return;
    }

    contentStyle.current = {
      top: contentStyle.current.top - scrollStep,
    };

    contentElement.style.top = `${contentStyle.current.top}px`;
    contentElement.style.transition = `${time}ms ${transitionFunction}`;

    toggleContentScroll(true);

    setTimeout(() => toggleContentScroll(false), time);
  }, [
    currentRowIndex,
    gridState.focusRowIndex,
    gridState.sizes.rowHeight,
    gridState.sizes.rowsNumber,
    isScrollBlocked,
  ]);

  const addPointToGazeMap = () => {
    let { x, y, r } = gazePosition.current;

    x = Math.round(x);
    y -= Math.round(contentRef.current.getBoundingClientRect().top);

    const pointsLength = Object.keys(gazeMap.current.points).length;
    const lastPoint = gazeMap.current.points[Object.keys(gazeMap.current.points)[pointsLength - 1]];

    if (lastPoint) {
      const dx = x - lastPoint.x;
      const dy = y - lastPoint.y;
      const d = Math.sqrt(dx * dx + dy * dy);

      if (d <= r + lastPoint.r) return;
    }

    gazeMap.current.points[new Date() - startDate.current] = {
      x,
      y,
      r,
    };
  };

  const gazeMove = useCallback(() => {
    if (
      !contentRef.current ||
      (gazePosition.current.x === gazePosition.current.px && gazePosition.current.y === gazePosition.current.py)
    ) {
      return;
    }

    addPointToGazeMap();

    if (currentRowIndex.current === gridState.focusRowIndex && !isScrollBlocked.current) {
      toggleContentScroll(true);

      setTimeout(() => {
        toggleContentScroll(false);
      }, 1500);
    }

    const gridRect = document.querySelector('.grid').getBoundingClientRect();
    let newCurrentRowIndex = null;

    for (let i = 0; i < gridState.sizes.rowsNumber; i++) {
      const y1 = i * gridState.sizes.rowHeight + gridRect.top;
      const y2 = y1 + gridState.sizes.rowHeight;

      if (gazePosition.current.y >= y1 && (gazePosition.current.y < y2 || i === gridState.sizes.rowsNumber - 1)) {
        if (i === currentRowIndex.current) {
          newCurrentRowIndex = i;
          break;
        }

        newCurrentRowIndex = i;
        break;
      }
    }

    currentRowIndex.current = newCurrentRowIndex;

    autoScrollContent();

    gazePosition.current.px = gazePosition.current.x;
    gazePosition.current.py = gazePosition.current.y;
  }, [autoScrollContent, gridState.focusRowIndex, gridState.sizes.rowHeight, gridState.sizes.rowsNumber]);

  const groupBlocks = useCallback(() => {
    if (
      blocks &&
      gridState.sizes.rowHeight &&
      (!blocksGroups || JSON.stringify(blocks) !== JSON.stringify(prevBlocks))
    ) {
      const groups = [];
      let groupIndex = 0;

      blocks.forEach((block_, index) => {
        const block = { ...block_, index };

        if (!groups[groupIndex]) {
          groups[groupIndex] = {
            width: 0,
            blocks: [],
          };
        }

        const blockWidth = block.type === 'text' ? block.params.textWidth : block.params.width;
        const currentGroup = groups[groupIndex];

        // if (
        //   (blockWidth > gridState.sizes.columnsNumber - 2 && currentGroup.width === 0) ||
        //   (currentGroup.width + blockWidth <= gridState.sizes.columnsNumber &&
        //     (!currentGroup.blocks.length || block.type === currentGroup.blocks.slice(-1)[0].type))
        // ) {
        if (
          (blockWidth > gridState.sizes.columnsNumber - 2 && currentGroup.width === 0) ||
          currentGroup.width + blockWidth <= gridState.sizes.columnsNumber
        ) {
          groups[groupIndex].width += blockWidth;
          groups[groupIndex].blocks.push(block);
        } else {
          groups.push({
            width: blockWidth,
            blocks: [block],
          });
          groupIndex++;
        }
      });

      setBlocksGroups(groups);
    }
  }, [blocks, blocksGroups, gridState.sizes.columnsNumber, gridState.sizes.rowHeight, prevBlocks]);

  useEffect(() => {
    groupBlocks();

    if (modes.indexOf('result') === -1 && contentRef.current && autoScroll && interval.current === null) {
      window.onmousemove = e => {
        gazePosition.current.x = e.clientX;
        gazePosition.current.y = e.clientY;
      };

      interval.current = setInterval(() => {
        gazeMove();
      }, intervalSpeed);
    }

    if (shouldUpdate.current === 1) {
      shouldUpdate.current++;
      window.onresize();
    }

    contentStyle.current.top = gridState.sizes.rowHeight * (gridState.focusRowIndex - 1);
  }, [autoScroll, gazeMove, gridState.focusRowIndex, gridState.sizes.rowHeight, groupBlocks, modes]);

  useEffect(() => {
    return () => {
      clearInterval(interval.current);
      setBlocksGroups(null);
    };
  }, []);

  return blocksGroups ? (
    <div
      ref={contentRef}
      className="content"
      style={{
        ...contentStyle.current,
        paddingBottom: autoScroll ? 0 : `${gridState.sizes.rowHeight * (gridState.focusRowIndex - 1)}px`,
      }}
    >
      {blocksGroups.map((blocksGroup, blocksGroupIndex) => (
        <div key={blocksGroupIndex} className="content-blocks-group">
          {blocksGroup.blocks.map((block, blockIndex) => {
            const style = {};

            if (blocksGroup.blocks.length > 1 && blockIndex > 0) {
              style.marginLeft = 'var(--grid-gutter)';
            }

            if (blockEditorState.highlightedBlockId && block.id !== blockEditorState.highlightedBlockId) {
              style.opacity = 0.5;
            }

            return (
              <div
                key={blockIndex}
                ref={el => {
                  if (!autoScroll || block.type === 'image' || !el || !el.previousSibling) {
                    return;
                  }

                  setTimeout(() => {
                    const childIndex = el.previousSibling.childNodes.length > 1 ? 1 : 0;
                    const elRect = el.childNodes[childIndex].getBoundingClientRect();
                    const elTextRect = el.querySelector('.text-container').getBoundingClientRect();
                    const height = Math.abs(elTextRect.top) - Math.abs(elRect.top);
                    const prevHeight = el.previousSibling.childNodes[childIndex].offsetHeight;
                    const prevMarginTop = window.getComputedStyle(el.previousSibling).marginTop;

                    el.style.marginTop = `calc(${prevHeight}px - ${height}px - 3 * var(--grid-row-height) + ${prevMarginTop})`;

                    if (shouldUpdate.current === 0 && prevHeight === 0) {
                      shouldUpdate.current++;
                    }
                  }, initializeTime);
                }}
                className={`content-block-container ${
                  block.id === blockEditorState.highlightedBlockId ? 'highlighted-block' : ''
                }`}
                style={style}
              >
                <Block
                  block={block}
                  canMoveUp={block.index !== 0}
                  canMoveDown={block.index !== blocks.length - 1}
                  modes={modes}
                  fontSizes={fontSizes}
                  setFontSizes={setFontSizes}
                  enableAnimation={enableAnimation}
                />
              </div>
            );
          })}
        </div>
      ))}
      {modes.indexOf('view') !== -1 && (
        <div className="content-blocks-group">
          <Button
            variant="contained"
            color="primary"
            disableElevation
            onClick={() => {
              console.log(JSON.stringify({ contentMarkup: contentMarkup.current, gazeMap: gazeMap.current }));
            }}
          >
             
          </Button>
        </div>
      )}
    </div>
  ) : null;
};
