import React, { useRef } from 'react';
// import './Test.css';

const blockGroups = [
  {
    blocks: [
      {
        width: 12,
        background: 'rgba(255, 0, 255, 0.1)',
        text: [
          '    ,      ,        ,    ,         ,    ,  ,    .      ,        ,       ,       .     ,         ,      .      ,   ,   .',
        ],
      },
    ],
  },
  {
    blocks: [
      {
        width: 4,
        background: 'rgba(255, 0, 0, 0.1)',
        text: [
          '       ,               ,   .          .  ,   ,          .     ,      .',
        ],
      },
      {
        width: 8,
        background: 'rgba(0, 255, 0, 0.1)',
        text: [
          '    ,      ,        ,    ,         ,    ,  ,    .      ,        ,       ,       .     ,         ,      .      ,   ,   .',
        ],
      },
    ],
  },
];

export const Test = () => {
  const maxWidth = 1280;
  const atom = 8;
  const gutter = atom * 4;
  const lineHeight = gutter - atom;

  document.documentElement.style.setProperty('--line-height', `${lineHeight}px`);

  const fontSize = lineHeight - atom;

  document.documentElement.style.setProperty('--font-size', `${fontSize}px`);

  const columnsNumber = 12;
  const columnSize = Math.floor((maxWidth - gutter * (columnsNumber - 1)) / columnsNumber);

  document.documentElement.style.setProperty('--column-size', `${columnSize}px`);

  const calcBlockWidth = cn => {
    return cn * columnSize + gutter * (cn - 1);
  };

  const containerWidth = calcBlockWidth(columnsNumber);

  const calcPadding = (ud, lr) => {
    return `${ud * lineHeight}px ${lr * gutter}px`;
  };

  const columns = [];

  for (let i = 0; i < columnsNumber; i++) {
    columns.push(<div className="grid-columns-column-" style={{ width: columnSize }}></div>);
  }

  const contentRef = useRef(null);
  const rows = [];

  if (contentRef.current) {
    const rowsNumber = contentRef.current.clientHeight / lineHeight;

    for (let i = 0; i < rowsNumber; i++) {
      rows.push(<div className="grid-rows-row-" style={{ height: lineHeight }}></div>);
    }
  }

  return (
    <div ref={contentRef} className="content-" style={{ width: containerWidth, maxWidth }}>
      <div className="grid-">
        <div className="grid-columns-" style={{ width: containerWidth }}>
          {columns.map((column, key) => ({ ...column, key }))}
        </div>
        <div className="grid-rows-">{rows.map((row, key) => ({ ...row, key }))}</div>
      </div>
      <div className="blocks-">
        {blockGroups.map((blockGroup, gi) => (
          <div key={gi} className="blocks-group-">
            {blockGroup.blocks.map((block, bi) => {
              // let marginLeft = bi > 0 ? gutter : 0;
              let marginLeft = 0;

              return (
                <div
                  key={bi}
                  className="block-"
                  style={{
                    width: calcBlockWidth(block.width),
                    background: block.background,
                    marginLeft,
                  }}
                >
                  <div className="text-">
                    {block.text.map((p, pi) => (
                      <p
                        key={pi}
                        style={{ lineHeight: `${lineHeight}px`, padding: calcPadding(0, 1), margin: calcPadding(1, 0) }}
                      >
                        {p}
                      </p>
                    ))}
                  </div>
                </div>
              );
            })}
          </div>
        ))}
      </div>
    </div>
  );
};

