import React from 'react';
import { Image } from '../Image/Image';
import { Text } from '../Text/Text';
import './TextImageHorizontal.css';

export const TextImageHorizontal = ({ block, fontSizes, setFontSizes }) => {
  const items = [
    <Text key={0} block={block} fontSizes={fontSizes} setFontSizes={setFontSizes} />,
    <Image key={1} block={block} />,
  ];

  if (block.image.position === 'left') {
    [items[0], items[1]] = [items[1], items[0]];
  }

  return items.map((item, index) => ({ ...item, key: index }));
};
