import React from 'react';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector, batch } from 'react-redux';
import { Slider, Typography, IconButton, Button, Divider } from '@material-ui/core';
import { Close } from '@material-ui/icons';
import { Alert } from '@material-ui/lab';
import { useStyles } from '../LeftDrawer/LeftDrawer.style';

import { setGridSizes, setGridFocusRowIndex } from '../../redux/Grid/grid.actions';

const atom = 8;
const gridSizesSettings = [
  {
    param: 'maxWidth',
    label: '   ',
    min: 1000,
    max: 1920,
    step: 10,
  },
  {
    param: 'gutter',
    label: '      ',
    min: atom,
    max: atom * atom,
    step: atom,
  },
  {
    param: 'rowHeight',
    label: ' ',
    min: atom * 3,
    max: atom * 6,
    step: atom,
  },
  {
    param: 'minFontSize',
    label: '  ',
    min: 14,
    max: 24,
    step: 1,
  },
];

const createSliderMarks = (min, max, accuracy = 0) => {
  return [
    {
      value: min,
      label: min.toFixed(accuracy),
    },
    {
      value: max,
      label: max.toFixed(accuracy),
    },
  ];
};

export const ContentSettings = () => {
  const dispatch = useDispatch();
  const gridState = useSelector(state => state.grid);
  const classes = useStyles();
  const { sizes } = gridState;

  const handleGridSizes = (param, value, prevValue) => {
    if (value === prevValue) {
      return;
    }

    batch(() => {
      dispatch(setGridSizes({ ...sizes, [param]: value }));
      window.onresize();
    });
  };

  return sizes && sizes.rowHeight ? (
    <div className="content-settings">
      <div className={classes.drawerItem}>
        <div className={classes.drawerHeader}>
          <Typography variant="h6">  </Typography>
          <IconButton component={Link} to="/admin">
            <Close />
          </IconButton>
        </div>
        <Button variant="outlined" color="primary" size="small" onClick={() => {}}>
          
        </Button>
      </div>
      <Divider />
      <div className={classes.drawerItem}>
        <Typography className={classes.drawerItemTitle} variant="h6">
           
        </Typography>
        <Alert className={classes.info} severity="info">
              ,       .
        </Alert>
        {gridSizesSettings.map(setting => (
          <div key={setting.param} className={classes.drawerItemParam}>
            <Typography variant="body1">{setting.label}</Typography>
            <Slider
              color="primary"
              value={sizes[setting.param]}
              min={setting.min}
              max={setting.max}
              step={setting.step}
              marks={createSliderMarks(setting.min, setting.max)}
              valueLabelDisplay="auto"
              onChange={(e, value) => handleGridSizes(setting.param, value, sizes[setting.param])}
            />
          </div>
        ))}
        <div className={classes.drawerItemParam}>
          <Typography variant="body1">  </Typography>
          <Slider
            color="primary"
            value={gridState.focusRowIndex + 1}
            min={2}
            max={sizes.rowsNumber}
            step={1}
            marks={createSliderMarks(2, sizes.rowsNumber)}
            valueLabelDisplay="auto"
            onChange={(e, value) => dispatch(setGridFocusRowIndex(value - 1))}
          />
        </div>
      </div>
    </div>
  ) : null;
};
