import * as types from './content.types';
import { getScenarioById } from '../Data/data.actions';

export const setContentContainerSizes = (containerWidth, containerHeight) => dispatch => {
  dispatch({
    type: types.SET_CONTENT_CONTAINER_SIZES,
    payload: { containerWidth, containerHeight },
  });
};

export const setScenario = id => (dispatch, getState) => {
  const { data } = getState();

  if (!data.scenarios) {
    return;
  }

  dispatch({ type: types.SET_SCENARIO, payload: dispatch(getScenarioById(id)) });
};

export const setScenarioBlock = block => (dispatch, getState) => {
  const { content } = getState();

  content.scenario.blocks[block.id] = block;

  dispatch({ type: types.SET_SCENARIO, payload: content.scenario });
};

export const setScenarioBlocks = blocks => (dispatch, getState) => {
  const { content } = getState();

  content.scenario.blocks = blocks;

  dispatch({ type: types.SET_SCENARIO, payload: content.scenario });
};

export const setScenarioParam = (param, value) => dispatch => {
  dispatch({ type: types.SET_SCENARIO_PARAM, payload: { [param]: value } });
};
