import React from 'react';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { List, ListItem, ListItemText, Divider, Collapse, IconButton } from '@material-ui/core';
import { Add } from '@material-ui/icons';
import { useStyles } from '../LeftDrawer/LeftDrawer.style';

export const AdminSidebar = () => {
  const classes = useStyles();
  const dataState = useSelector(state => state.data);
  const { scenarios } = dataState;

  return scenarios ? (
    <List disablePadding>
      <ListItem className={classes.listItem} button component={Link} to="/admin/content-settings">
        <ListItemText primary="   " />
      </ListItem>
      <Divider />
      <ListItem className={classes.listItem}>
        <ListItemText primary="" />
        <IconButton size="small">
          <Add />
        </IconButton>
      </ListItem>
      <Collapse in={true} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>
          {Object.entries(scenarios).map(([, scenario]) => (
            <ListItem
              key={scenario.id}
              className={classes.nestedListItem}
              button
              component={Link}
              to={`/admin/scenarios/${scenario.id}`}
            >
              <ListItemText primary={scenario.title} />
            </ListItem>
          ))}
        </List>
      </Collapse>
      <Divider />
      <ListItem className={classes.listItem} button component={Link} to="/admin/users">
        <ListItemText primary="" />
      </ListItem>
      <Divider />
    </List>
  ) : null;
};
