import React, { useState, useEffect, useCallback } from 'react';
import { useLocation } from 'react-router-dom';
import { useDispatch, useSelector, batch } from 'react-redux';
import { Button } from '@material-ui/core';
import { Add } from '@material-ui/icons';

import { usePrevious } from '../../hooks';
import { resizeGrid } from '../../redux/Grid/grid.actions';
import { toggleAddBlockDialog } from '../../redux/BlockEditor/blockEditor.actions';
import { setContentContainerSizes } from '../../redux/Content/content.actions';
import { useStyles } from './ContentContainer.style';

import { Grid } from '../Grid/Grid';
import { Content } from '../Content/Content';

import './ContentContainer.css';

export const ContentContainer = ({ autoScroll = false, blocks, modes }) => {
  const classes = useStyles();
  const location = useLocation();
  const dispatch = useDispatch();
  const contentState = useSelector(state => state.content);
  const gridState = useSelector(state => state.grid);
  const prevGridSizes = usePrevious({ ...gridState.sizes });

  const [readingProgress, setReadingProgress] = useState(null);

  const setOnresizeEvent = useCallback(() => {
    if (
      gridState.initialized &&
      (!window.onresize || JSON.stringify(gridState.sizes) !== JSON.stringify(prevGridSizes))
    ) {
      window.onresize = () => {
        const contentContainer = document.querySelector('.content-container');
        const contentContainerSupport = document.querySelector('.content-container-support');

        if (!contentContainerSupport) {
          return;
        }

        const { clientHeight } = contentContainerSupport;
        const width = Math.min(gridState.sizes.maxWidth, contentContainer.clientWidth);

        batch(() => {
          dispatch(setContentContainerSizes(width, clientHeight));
          dispatch(resizeGrid());
        });
      };

      window.onresize();
    }
  }, [dispatch, gridState.initialized, gridState.sizes, prevGridSizes]);

  useEffect(() => {
    if (blocks) {
      if (!readingProgress) {
        const progress = [];

        blocks.forEach(block => {
          if (!block.text) return;

          block.text.forEach(() => {
            progress.push({
              completed: false,
            });
          });
        });

        setReadingProgress(progress);
      }

      setOnresizeEvent();
    }
  }, [blocks, readingProgress, setOnresizeEvent]);

  useEffect(() => {
    return () => {
      window.onresize = null;
    };
  }, []);

  return (
    <div
      className={`content-container ${autoScroll ? 'auto-scroll-content' : ''} ${modes.join(' ')}`}
      style={{ justifyContent: blocks && blocks.length ? 'flex-start' : 'center' }}
    >
      {blocks && blocks.length ? (
        <>
          <div
            className="content-container-support"
            style={{ width: contentState.containerWidth, maxWidth: gridState.sizes.maxWidth }}
          >
            {modes.indexOf('edit') !== -1 && (
              <div className="borders" style={{ maxWidth: contentState.containerWidth + 2 }} />
            )}
            <Grid show={location.pathname.split('/').indexOf('content-settings') !== -1} />
            <Content modes={modes} blocks={blocks} autoScroll={autoScroll} />
          </div>
          {modes.indexOf('view') !== -1 && readingProgress && (
            <div className="reading-progress-container">
              <div className="reading-progress">
                {readingProgress.map((item, index) => (
                  <div
                    key={index}
                    className={`reading-progress-item ${item.completed ? 'completed' : 'not-completed'}`}
                  />
                ))}
              </div>
            </div>
          )}
        </>
      ) : (
        <Button
          className={classes.addButton}
          variant="outlined"
          color="primary"
          onClick={() => dispatch(toggleAddBlockDialog())}
        >
          <Add />
          <span> </span>
        </Button>
      )}
    </div>
  );
};
