import React, { useEffect } from 'react';
import { Route, Switch } from 'react-router';
import { useDispatch, useSelector } from 'react-redux';
import { fetchGridParams } from '../../redux/Grid/grid.actions';
import { fetchData } from '../../redux/Data/data.actions';
import { Admin } from '../Admin/Admin';
import { Scenario } from '../Scenario/Scenario';
import { Header } from '../Header/Header';
import { Scenarios } from '../Scenarios/Scenarios';
import './App.css';
import { Test } from './Test';

export const App = () => {
  const dispatch = useDispatch();
  const dataState = useSelector(state => state.data);
  const gridState = useSelector(state => state.grid);

  useEffect(() => {
    if (!dataState.gridParams) {
      dispatch(fetchData());
    } else if (!gridState.initialized) {
      dispatch(fetchGridParams());
    }
  });

  return (
    <div className="App">
      <Header />
      <Switch>
        <Route path="/admin">
          <Admin />
        </Route>
        <Route exact path={['/', '/scenarios']}>
          <Scenarios />
        </Route>
        <Route exact path="/scenarios/:scenarioId">
          <Scenario modes={['view']} autoScroll={true} />
        </Route>
        <Route exact path="/test">
          <Test />
        </Route>
      </Switch>
    </div>
  );
};
