import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { BlockActions } from '../BlockActions/BlockActions';
import { Image } from '../Image/Image';
import { Text } from '../Text/Text';
import { TextImageHorizontal } from '../TextImageHorizontal/TextImageHorizontal';
import { TextImageVertical } from '../TextImageVertical/TextImageVertical';
import { calcBlockWidth } from '../../redux/Grid/grid.actions';
import { FadeInBlock } from '../FadeInBlock/FadeInBlock';

import './Block.css';

export const Block = ({ modes, block, canMoveUp, canMoveDown, fontSizes, setFontSizes, enableAnimation }) => {
  const dispatch = useDispatch();
  const blockEditorState = useSelector(state => state.blockEditor);
  const block_ =
    blockEditorState.selectedBlock && blockEditorState.selectedBlock.id === block.id
      ? blockEditorState.selectedBlock
      : block;

  const renderBlock = block => {
    const style = {
      width: block.params.width ? `${dispatch(calcBlockWidth(block.params.width))}px` : 'auto',
    };

    return (
      <div className={`content-block content-block-type--${block.type}`} style={style}>
        {(() => {
          switch (block.type) {
            case 'text':
              return <Text block={block} fontSizes={fontSizes} setFontSizes={setFontSizes} />;
            case 'image':
              return <Image block={block} />;
            case 'text-image-horizontal':
              return <TextImageHorizontal block={block} fontSizes={fontSizes} setFontSizes={setFontSizes} />;
            case 'text-image-vertical':
              return <TextImageVertical block={block} fontSizes={fontSizes} setFontSizes={setFontSizes} />;
            default:
              return null;
          }
        })()}
      </div>
    );
  };

  return block_ ? (
    modes.indexOf('view') === -1 ? (
      <>
        {modes.indexOf('edit') !== -1 && (
          <BlockActions block={block_} canMoveUp={canMoveUp} canMoveDown={canMoveDown} />
        )}
        {renderBlock({ ...block_ })}
      </>
    ) : (
      <FadeInBlock modes={modes} enableAnimation={enableAnimation}>
        {modes.indexOf('edit') !== -1 && (
          <BlockActions block={block_} canMoveUp={canMoveUp} canMoveDown={canMoveDown} />
        )}
        {renderBlock({ ...block_ })}
      </FadeInBlock>
    )
  ) : null;
};
