import React from 'react';
import { useSelector } from 'react-redux';
import './Image.css';

export const Image = ({ block }) => {
  const gridState = useSelector(state => state.grid);

  return (
    <div
      className="content-block-item image"
      style={{
        backgroundImage: `url(${block.image.src})`,
        backgroundSize: block.image.size,
        height: block.image.height ? `calc(var(--grid-row-height) * ${block.image.height})` : 'auto',
        top:
          block.type === 'text-image-horizontal' ? `calc(var(--grid-row-height) * ${gridState.focusRowIndex})` : 'auto',
      }}
    ></div>
  );
};
