import React, { useEffect } from 'react';
import { useParams } from 'react-router';
import { useDispatch, useSelector } from 'react-redux';
import { ContentContainer } from '../ContentContainer/ContentContainer';
import { AddBlockDialog } from '../AddBlockDialog/AddBlockDialog';
import { setScenario } from '../../redux/Content/content.actions';

export const Scenario = ({ modes, autoScroll }) => {
  const dispatch = useDispatch();
  const contentState = useSelector(state => state.content);
  const dataState = useSelector(state => state.data);
  const { scenario } = contentState;
  const blocks = scenario ? scenario.blocks : null;
  const { scenarioId } = useParams();

  useEffect(() => {
    if (dataState.scenarios && (!scenario || scenario.id !== scenarioId)) {
      dispatch(setScenario(scenarioId));
    }
  }, [scenario, scenarioId, dataState.scenarios, dispatch]);

  /* eslint-disable */
  useEffect(() => {
    return () => {
      dispatch(setScenario());
    };
  }, []);
  /* eslint-enable */

  return scenario ? (
    <>
      <ContentContainer
        autoScroll={autoScroll}
        blocks={Object.keys(blocks)
          .reduce((arr, blockId) => {
            arr.push(blocks[blockId]);
            return arr;
          }, [])
          .sort((b1, b2) => b1.order - b2.order)}
        modes={modes}
      />
      {modes.indexOf('edit') !== -1 && <AddBlockDialog />}
    </>
  ) : null;
};
