import { combineReducers } from 'redux';
import { dataReducer as data } from './Data/data.reducer';
import { gridReducer as grid } from './Grid/grid.reducer';
import { contentReducer as content } from './Content/content.reducer';
import { blockEditorReducer as blockEditor } from './BlockEditor/blockEditor.reducer';

export const rootReducer = combineReducers({
  data,
  grid,
  content,
  blockEditor,
});
