import React from 'react';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { Button, TextField, Typography, Divider, IconButton } from '@material-ui/core';
import { Close } from '@material-ui/icons';
import { useStyles } from '../LeftDrawer/LeftDrawer.style';
import { setScenarioParam } from '../../redux/Content/content.actions';

export const ScenarioEditor = () => {
  const dispatch = useDispatch();
  const contentState = useSelector(state => state.content);
  const { scenario } = contentState;
  const classes = useStyles();

  const handleScenarioTitle = e => {
    const title = e.target.value;

    dispatch(setScenarioParam('title', title));
  };

  return scenario ? (
    <div className="scenario-editor">
      <div className={classes.drawerItem}>
        <div className={classes.drawerHeader}>
          <Typography variant="h6"> </Typography>
          <IconButton component={Link} to={`/admin`}>
            <Close />
          </IconButton>
        </div>
        <Button
          className={classes.saveButton}
          variant="outlined"
          color="primary"
          size="small"
          component={Link}
          to={`/admin`}
        >
          
        </Button>
      </div>
      <Divider />
      <div className={classes.drawerItem}>
        <div className={classes.drawerItemParam}>
          <div className={classes.drawerItemParam}>
            <TextField
              label=" "
              variant="outlined"
              fullWidth
              value={scenario.title}
              onChange={handleScenarioTitle}
            />
          </div>
        </div>
      </div>
    </div>
  ) : null;
};
