import React from 'react';
import { useHistory } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { nanoid } from 'nanoid';
import { Dialog, DialogTitle, List, ListItem, ListItemAvatar, Avatar, ListItemText } from '@material-ui/core';
import { Notes, Image, VerticalSplit, HorizontalSplit } from '@material-ui/icons';
import { toggleAddBlockDialog } from '../../redux/BlockEditor/blockEditor.actions';
import { setScenarioBlocks } from '../../redux/Content/content.actions';
import { useStyles } from './AddBlockDialog.style';

const blocks = [
  {
    type: 'text',
    name: '',
    icon: <Notes />,
  },
  {
    type: 'image',
    name: '',
    icon: <Image />,
  },
  {
    type: 'text-image-horizontal',
    name: '     ',
    icon: <VerticalSplit />,
  },
  {
    type: 'text-image-vertical',
    name: '     ',
    icon: <HorizontalSplit style={{ transform: 'rotate(180deg)' }} />,
  },
];

export const AddBlockDialog = () => {
  const history = useHistory();
  const classes = useStyles();
  const dispatch = useDispatch();
  const blockEditorState = useSelector(state => state.blockEditor);
  const contentState = useSelector(state => state.content);
  const { scenario } = contentState;
  const gridState = useSelector(state => state.grid);
  const { addBlockDialog } = blockEditorState;

  const handleClose = () => {
    dispatch(toggleAddBlockDialog());
  };

  const addBlock = type => {
    const blocks = { ...scenario.blocks };
    let order = 0;

    if (addBlockDialog.order) {
      order = addBlockDialog.order;

      if (addBlockDialog.direction > 0) {
        for (const [, block] of Object.entries(blocks)) {
          if (block.order <= order) {
            block.order--;
          }
        }
      } else {
        for (const [, block] of Object.entries(blocks)) {
          if (block.order >= order) {
            block.order++;
          }
        }
      }
    }

    const block = {
      id: nanoid(),
      order,
      type,
      params: {},
    };

    if (type.indexOf('image') !== -1) {
      block.image = {
        src: '',
        size: 'cover',
      };

      if (type === 'image') {
        block.params.width = gridState.sizes.columnsNumber;
      }

      if (type === 'text-image-horizontal') {
        block.image.height = 0;
      } else {
        block.image.height = Math.floor(gridState.sizes.rowsNumber / 3);
      }
    }

    if (type.indexOf('text') !== -1) {
      block.title = '  ';
      block.text = ['  '];

      if (type === 'text-image-horizontal') {
        block.params.width = gridState.sizes.columnsNumber;
        block.params.textWidth = Math.floor(0.61803 * block.params.width);
      } else if (type === 'text-image-vertical') {
        block.params.width = Math.floor(gridState.sizes.columnsNumber / 3);
      } else {
        block.params.textWidth = gridState.sizes.columnsNumber;
      }
    }

    blocks[block.id] = block;

    handleClose();
    dispatch(setScenarioBlocks(blocks));
    history.push(`/admin/scenarios/${scenario.id}/block-editor/${block.id}`);
  };

  return (
    <Dialog onClose={handleClose} open={addBlockDialog.show}>
      <DialogTitle> </DialogTitle>
      <List>
        {blocks.map(block => (
          <ListItem key={block.type} button onClick={() => addBlock(block.type)}>
            <ListItemAvatar>
              <Avatar className={classes.avatar}>{block.icon}</Avatar>
            </ListItemAvatar>
            <ListItemText primary={block.name} />
          </ListItem>
        ))}
      </List>
    </Dialog>
  );
};
