import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { List, ListItem, ListItemText, Divider, Collapse, IconButton, Typography } from '@material-ui/core';
import { Close } from '@material-ui/icons';
import { useStyles } from '../LeftDrawer/LeftDrawer.style';

export const UsersList = () => {
  const classes = useStyles();
  const dataState = useSelector(state => state.data);
  const { users, userGroups } = dataState;
  const [groups, setGroups] = useState(null);

  useEffect(() => {
    if (users && userGroups && !groups) {
      const grs = {
        0: {
          id: 0,
          name: ' ',
          users: {},
        },
      };

      Object.entries(userGroups).forEach(([, userGroup]) => {
        grs[userGroup.id] = { ...userGroup, users: {} };
      });

      for (const [, user] of Object.entries(users)) {
        if (!user.groupId) {
          grs['0'].users[user.id] = user;
          continue;
        }

        grs[user.groupId].users[user.id] = user;
      }

      setGroups(grs);
    }
  }, [groups, userGroups, users]);

  useEffect(() => {
    return () => {
      setGroups(null);
    };
  }, []);

  return groups ? (
    <div className="users">
      <div className={classes.drawerItem}>
        <div className={classes.drawerHeader}>
          <Typography variant="h6"></Typography>
          <IconButton component={Link} to="/admin">
            <Close />
          </IconButton>
        </div>
      </div>
      <Divider />
      <List disablePadding>
        {Object.entries(groups).map(([, group]) => (
          <React.Fragment key={group.id}>
            <ListItem className={classes.listItem}>
              <ListItemText primary={group.name} />
            </ListItem>
            <Collapse in={true} timeout="auto" unmountOnExit>
              <List component="div" disablePadding>
                {Object.entries(group.users).map(([, user]) => (
                  <ListItem
                    key={user.id}
                    className={classes.nestedListItem}
                    button
                    component={Link}
                    to={`/admin/users/${user.id}`}
                  >
                    <ListItemText primary={user.name} />
                  </ListItem>
                ))}
              </List>
            </Collapse>
            <Divider />
          </React.Fragment>
        ))}
      </List>
    </div>
  ) : null;
};
