import React from 'react';
import { GazeMap } from '../GazeMap/GazeMap';
import monitoringResult from './result.json';

export const MonitoringResult = () => {
  return (
    <div style={{ position: 'relative', width: '100%', height: '100%', overflow: 'auto' }}>
      <GazeMap monitoringResult={monitoringResult} />
    </div>
  );
};
